    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="place_id" label="渠道推广ID" align="center" />
      <el-table-column prop="name" label="渠道名称" align="center" />
      <el-table-column prop="total_registerd_num" label="总注册数" align="center" />
      <el-table-column prop="today_registerd_num" label="今日" align="center" />
      <el-table-column prop="yesterday_registerd_num" label="昨日" align="center" />
      <el-table-column prop="current_month_registerd_num" label="当月" align="center" />
      <el-table-column prop="prev_month_registerd_num" label="上月" align="center" />
      <el-table-column align="center" label="操作" width="150" fixed="right">
        <template slot-scope="scope">
          <el-button size="small" @click="editItem(scope.row.id)" type="primary">编辑</el-button>
          <!-- <el-button size="small" type="danger" @click="removeFreeze(scope.row.id)">停止</el-button> -->
        </template>
      </el-table-column>
    </el-table>
