      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import dayjs from 'dayjs'
import Fetch from '../../utils/fetch'
import Fetch4 from '../../utils/fetch4'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      count: 0,
      page_curren: 1,
      searchs: {
        keyword_search_value: '',
        status: '',
        is_auth: '',
        reg_ip: '',
        begin_addtime: '',
        end_addtime: '',
        channel: '',
        level: '',
      },
      config: {
        status: {},
        is_auth: {},
        channel: {},
      },

      rules: {},
      channel_info: '',
      tableData: [],
      loading: false,
      online_user_num: 0,
      user_num: 0,
      dialogFormVisible: false,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      dialogLoading: false,
      addtime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    toIp(ip) {
      this.searchs.reg_ip = ip
      this.FetchList()
    },
    openCtrlMoney(uid) {
      this.moneyCtrl.id = uid
      this.dialogFormVisible = true
    },
    openCtrlPrize(uid) {
      this.prizeCtrl.id = uid
      this.dialogFormVisible1 = true
    },
    openCtrlAnwser(uid) {
      this.anwserCtrl.id = uid
      this.dialogFormVisible2 = true
    },
    // 创建
    createPlace() {
      this.$router.push('/place/create')
    },
    FetchList() {
      this.loading = true
      Fetch('/place/place/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.config = r.data.config
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.user_num = r.data.user_num
          this.online_user_num = r.data.online_user_num
          this.channel_info = r.data.channel_info
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(id) {
      this.$router.push('/place/edit/' + id)
    },
    removeFreeze(id) {
      this.$confirm('此操作将停止计算该渠道, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          Fetch('/place/place/status/' + id).then(() => {
            this.FetchList()
            this.$message({
              type: 'success',
              message: '冻结成功!',
            })
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消冻结',
          })
        })
    },
  },
}
</script>
